.code-editor {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.textarea {
  font-family: 'Courier New', monospace;
  font-size: 14px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.code-output {
  font-family: 'Courier New', monospace;
  font-size: 14px;
  padding: 10px;
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 5px;
  white-space: pre-wrap;
}